.w--button{font-size:14px;padding:10px 15px;border-radius:4px;-webkit-transition:all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);transition:all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);display:inline-block;cursor:pointer;-webkit-box-shadow:none;box-shadow:none;outline:none;border:none;line-height:1.42;letter-spacing:1px}.w--button:empty{background-color:transparent;border:1px solid;-webkit-box-shadow:none;box-shadow:none}.w--button--block{display:block;width:100%}.w--button--black{background-color:#030303;color:#fff}.w--button--bordered{border:1px solid #030303;color:#030303;border-radius:4px;background-color:#fff}.w--button--bordered:hover{-webkit-box-shadow:0 1px 16px 0 rgba(107,122,254,0.3);box-shadow:0 1px 16px 0 rgba(107,122,254,0.3)}.w--button--medium{height:37px;font-size:16px}.w--button--large{height:60px;font-size:20px;font-weight:bold;font-size:18px;padding-left:20px}.w--button--block{width:100%}.w--button--orange{background:#fc5846;background-image:linear-gradient(357.04deg, #ec5f67 0%, #ff704c 100%);color:#fff;padding:12px 24px;border:0;text-decoration:none;-webkit-box-shadow:0 2px 4px 0 rgba(0,0,0,0.5);box-shadow:0 2px 4px 0 rgba(0,0,0,0.5)}.w--button:hover .right-arrow{right:20px}@media screen and (max-width: 768px){.w--button{height:46px;-webkit-box-shadow:0 2px 4px 0 rgba(0,0,0,0.5);box-shadow:0 2px 4px 0 rgba(0,0,0,0.5);text-transform:uppercase;font-size:14px}.w--button--empty{background-color:transparent;border:1px solid;-webkit-box-shadow:none;box-shadow:none}.w--button--medium{height:auto;font-size:16px}}
