.header-container .container{border-bottom:1px solid}.header-container .wrapper{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.header-container .wrapper img{width:150px;height:60px}.header-container .wrapper .logo{margin-left:50px}.header-container .cart{font-size:25px}.header-container .mobile-ham{position:absolute;top:15px;left:20px}.header-container .mobile-ham .icon{position:absolute;content:'';background-color:#fff;width:2px;height:25px;-webkit-transform:rotate(90deg);-ms-transform:rotate(90deg);transform:rotate(90deg)}.header-container .mobile-ham .icon:before{content:'';width:2px;height:25px;background-color:#fff;display:inline-block;position:absolute;left:8px;-webkit-transform:rotate(180deg);-ms-transform:rotate(180deg);transform:rotate(180deg)}.header-container .mobile-ham .icon:after{content:'';width:2px;height:25px;background-color:#fff;display:inline-block;position:absolute;right:8px;-webkit-transform:rotate(180deg);-ms-transform:rotate(180deg);transform:rotate(180deg)}@media only screen and (min-width: 768px){.header-container .container{border:0px}.header-container .mobile-ham{display:none}.header-container .cart{display:block;font-size:25px;margin-top:-5px}.header-container .wrapper{min-height:70px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.header-container .wrapper img{width:200px;height:80px}.header-container .wrapper .logo{text-align:left;font-size:2.5em;margin-left:0px}}
