.footer-container{background-color:#030303;color:#fff;padding:5px 0px}.footer-container .container{margin-top:15px;margin-bottom:15px}.footer-container ul{padding-left:0px}.footer-container ul li{cursor:pointer;position:relative;display:inline;padding:10px 15px 10px 0px;font-size:13px}.footer-container ul li:after{position:absolute;background-color:#fff;content:'';height:14px;width:2px;right:6px;top:11px}.footer-container .heads{text-transform:uppercase;letter-spacing:0.5px;margin:5px 10px 5px 0px}.footer-container .values{text-transform:capitalize}.footer-container .social li:after{display:none}@media only screen and (min-width: 768px){.footer-container .order li,.footer-container .company li{cursor:pointer;display:-webkit-box;display:-ms-flexbox;display:flex;padding-bottom:0px}.footer-container .order li:after,.footer-container .company li:after{display:none}.footer-container .container{padding:0px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start}}
